import clsx from "clsx"
import React, { } from "react"
import seedrandom from "seedrandom"
import { arrayRange, } from "ts3dutils"
import { RegularPolygon } from "./RegularPolygon"

export function SquareTiles({
  x: w,
  y: h,
  a,
  ordered,
  randomClassCount,
  ...props
}





) {
  const sr = seedrandom("X")
  const rx = () => "r" + Math.floor(sr() * randomClassCount)
  //viewBox="0 0 20 20"
  return (
    React.createElement('svg', { className: "adrian", ...props,}
      , arrayRange(0, w).flatMap((x) =>
        arrayRange(0, h).flatMap((y) => (
          React.createElement(RegularPolygon, {
            key: `sqr-${x}-${y}`,
            sides: 4,
            radius: (Math.SQRT1_2 * a) / 10,
            transform: `translate(${x} ${y}) rotate(45)`,
            className: clsx(rx(), "c0"),}
          )
        )),
      )
    )
  )
}
